.features-item {
  display: flex;
  flex-direction: column;

  padding: 84px 37px 20px 67px;
  box-shadow: 0 15px 34px 0 rgba(124, 151, 170, 0.2);
  background-color: #ffffff;

  width: calc(50% - 15px);
  min-height: 322px;
  position: relative;

  @media (max-width: 768px) {
    width: 100%;
  }

  @media (max-width: 576px) {
    padding-left: 20px;
    padding-top: 50px;
    padding-bottom: 40px;
    min-height: auto;
  }

  span {
    font-weight: 400;
    font-size: 1.6rem;
    line-height: 2.4rem;
    color: #999999;
  }

  &__title {
    font-weight: 700;
    font-size: 2.5rem;
    color: $secondary-color;

    margin-bottom: 20px;
  }

  &__icon {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 97px;
    height: 97px;
    border-radius: 50%;
    background-color: $primary-color;

    position: absolute;
    top: -62px;
    left: 68px;

    @media (max-width: 576px) {
      left: 8px;
    }

    svg {
      width: 46px;
      height: 46px;
      fill: $color-default-white;
    }
  }
}
