.reviews-foto {
   width: 100%;
   max-width: 370px;
   img {
      width: 100%;
      max-width: 370px;
      box-shadow: 0px 4px 16px 0 rgba(0, 0, 0, 0.1);
   }
   @media ( max-width: 1180px ) {
      display: none;
      max-width: 560px;
      margin: 0 auto;
      margin-top: 40px;
      img {
         max-width: 560px;
      }
   }
}